<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的题库</div>
    <tab-filter
      :tabs="tabs"
      :filters="filters"
      @tabChange="tabHandler"
      @filterChange="filterHandler"
    />
    <div class="personnal-detail-wrap-filter">
      <div class="personnal-detail-wrap-filter-label fl">筛选条件：</div>
      <div class="personnal-detail-wrap-filter-path fl">
        <div class="personnal-detail-wrap-filter-path-card fl" v-for="(s,i) in selected" :key="i">
          <span>{{s.text}}</span>
          <i class="iconfont icon-cha" @click="removeChoosed(s)"></i>
        </div>
      </div>
      <div class="fr">
        共&nbsp;
        <span class="red-text">{{total}}</span>
        &nbsp;题
      </div>
      <div class="clear"></div>
    </div>
    <common-table
      :columns="tableColumns"
      :datas="tableDatas"
      :pagger="false"
      th-class="table-innerhead"
      border
    >
      <template slot="rate" slot-scope="scope">
        <el-rate v-model="scope.value" disabled></el-rate>
      </template>
      <template slot="oprate" slot-scope="scope">
        <el-button type="primary" size="mini" @click="look(scope.row)">查看</el-button>
        <el-button type="danger" size="mini" @click="delete(scope.row)">删除</el-button>
      </template>
    </common-table>
    <bottom-pager :total="total" @change="pageChange"/>
  </div>
</template>
<script>
import tabFilter from "../../../components/tabFilter";
import commonTable from "../../../components/commonTable";
import bottomPager from "../../../components/personal/bottomPager";
export default {
  components: {
    tabFilter,
    commonTable,
    bottomPager
  },
  data() {
    return {
      total: 0,
      tabs: [
        {
          label: "我的错题",
          name: "falt"
        },
        {
          label: "收藏题型",
          name: "collect"
        },
        {
          label: "历年真题",
          name: "history"
        },
        {
          label: "我的试卷",
          name: "papers"
        },
        {
          label: "考点练习",
          name: "practice"
        },
        {
          label: "做题记录",
          name: "records"
        }
      ],
      filters: [
        {
          label: "科目",
          children: [
            {
              active: false,
              text: "大学英语"
            },
            {
              active: false,
              text: "大学语文"
            },
            {
              active: false,
              text: "高等数学"
            },
            {
              active: false,
              text: "C++"
            },
            {
              active: false,
              text: "马列主义"
            },
            {
              active: false,
              text: "会计实务"
            },
            {
              active: false,
              text: "电路原理"
            },
            {
              active: false,
              text: "运输安全系统工程"
            },
            {
              active: false,
              text: "机电一体化基础"
            }
          ]
        },
        {
          label: "年级",
          children: [
            {
              active: false,
              text: "大一上学期"
            },
            {
              active: false,
              text: "大一下学期"
            },
            {
              active: false,
              text: "大二上学期"
            },
            {
              active: false,
              text: "大二下学期"
            },
            {
              active: false,
              text: "大三上学期"
            },
            {
              active: false,
              text: "大三下学期"
            },
            {
              active: false,
              text: "大四上学期"
            },
            {
              active: false,
              text: "大四下学期"
            }
          ]
        }
      ],
      selected: [],
      tableColumns: [
        {
          prop: "title",
          label: "题干",
          width: 300
        },
        {
          prop: "type",
          label: "题型"
        },
        {
          prop: "myAnswer",
          label: "我的答案"
        },
        {
          prop: "rightAnswer",
          label: "正确答案"
        },
        {
          prop: "rate",
          label: "难度系数",
          slot: true,
          width: 160
        },
        {
          prop: "oprate",
          label: "操作",
          slot: true,
          width: 150
        }
      ],
      tableDatas: [
        {
          title: "Modern teaching equipment as well as some computers",
          type: "单选题",
          myAnswer: "A",
          rightAnswer: "D",
          rate: 4
        }
      ]
    };
  },
  created() {
    this.total = this.tableDatas.length;
  },
  methods: {
    tabHandler(key) {},
    filterHandler(selected) {
      this.selected = selected;
    },
    removeChoosed(s) {
      this.filters[s.parent].children.forEach(element => {
        if (element.text === s.text) {
          this.$set(element, "active", false);
        }
      });
      this.selected.splice(this.selected.indexOf(s), 1);
    },
    pageChange(page) {}
  }
};
</script>
<style lang="less" scoped>
.personnal-detail-wrap-filter {
  width: 100%;
  height: 23px;
  line-height: 23px;
  margin: 28px auto;
  &-label {
    font-size: 0.9rem;
    color: #aaaaaa;
  }
  &-path {
    &-card {
      height: 100%;
      padding: 0 8px;
      border: 1px solid #60b6fd;
      color: #60b6fd;
      border-radius: 6px;
      font-size: 0.9rem;
      margin-right: 16px;
      span {
        margin-right: 6px;
      }
      .iconfont {
        font-size: 0.66rem;
        cursor: pointer;
      }
    }
  }
}
</style>



