<template>
  <div class="tab-filter">
    <el-tabs class="tab-filter-tabs fl" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane v-for="tab in tabs" :key="tab.name" :label="tab.label" :name="tab.name"></el-tab-pane>
    </el-tabs>
    <div class="tab-filter-btn fr" @click="toggleFilter">
      <i class="iconfont icon-shaixuan"></i>
      {{btnText}}
    </div>
    <div class="clear"></div>
    <transition name="el-zoom-in-top">
      <div v-if="show" class="tab-filter-panel">
        <div class="tab-filter-panel-line" v-for="l in filters" :key="l.label">
          <div class="tab-filter-panel-line-label fl">{{l.label}}：</div>
          <div class="tab-filter-panel-line-box fl">
            <div
              class="tab-filter-panel-line-box-card fl"
              v-for="(c,i) in l.children"
              :key="i"
              :class="{'tab-filter-panel-line-box-card-active':c.active}"
              @click="clickHandler(l,c)"
            >{{c.text}}</div>
          </div>
          <div class="clear"></div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  props: {
    tabs: Array,
    filters: Array
  },
  data() {
    return {
      activeName: "",
      show: false,
      btnText: "展开"
    };
  },
  created() {
    this.activeName = this.tabs[0].name;
  },
  methods: {
    toggleFilter() {
      this.show = !this.show;
      this.btnText = this.show ? "收起" : "展开";
    },
    tabClick() {
      this.$emit("tabChange", this.activeName);
    },
    clickHandler(p, c) {
      p.children.forEach(element => {
        if (element === c) {
          this.$set(element, "active", !element.active);
        } else {
          this.$set(element, "active", false);
        }
      });
      this.getFilters();
    },
    getFilters() {
      let selected = [];
      this.filters.forEach((element, index) => {
        element.children.forEach(ele => {
          if (ele.active) {
            selected.push({
              parent: index,
              text: ele.text
            });
          }
        });
      });
      this.$emit("filterChange", selected);
    }
  }
};
</script>
<style lang="less">
.tab-filter {
  width: 100%;
  user-select: none;
  position: relative;
  &-tabs {
    .el-tabs__header {
      margin-bottom: 0;
    }
    .el-tabs__nav-wrap::after {
      background-color: transparent;
    }
    .el-tabs__item {
      font-size: 1rem;
    }
  }
  &-btn {
    padding: 0 6px;
    height: 22px;
    line-height: 22px;
    border: 1px solid #60b6fd;
    border-radius: 6px;
    font-size: 0.9rem;
    color: #60b6fd;
    margin-top: 9px;
    cursor: pointer;
    .iconfont {
      margin-right: 5px;
    }
  }
  &-panel {
    width: 100%;
    padding: 12px 36px;
    background: #ecf1f7;
    position: absolute;
    top: 40px;
    left: 0;
    z-index: 999;
    &-line {
      width: 100%;
      margin-bottom: 20px;
      font-size: 0.9rem;
      &-label {
        width: 50px;
        padding: 2px;
      }
      &-box {
        width: calc(100% - 50px);
        &-card {
          padding: 2px 6px;
          margin-right: 40px;
          margin-bottom: 12px;
          border-radius: 5px;
          cursor: pointer;
          &-active {
            background: #60b6fd;
            color: #fff;
          }
        }
      }
    }
  }
}
</style>



